import './editor.scss';
import './style.scss';
import {
    __experimentalLinkControl as LinkControl,
    AlignmentToolbar,
    BlockControls,
    RichText
} from '@wordpress/block-editor';
import {Button} from 'react-bootstrap';
import {__} from '@wordpress/i18n';
import {arrowDown, arrowLeft, arrowRight, arrowUp, more} from '@wordpress/icons';
import icon from './components/icon.js';
import {
    ComboboxControl,
    Dashicon,
    ExternalLink, FormFileUpload,
    Popover,
    SandBox,
    ToolbarButton,
    ToolbarDropdownMenu,
    ToolbarGroup,
} from '@wordpress/components';


const {Component, Fragment, useState} = wp.element;

const {registerBlockType, PlainText} = wp.blocks;

registerBlockType('hupa/bootstrap-button', {
    title: __('BS-Button', 'bootscore'),
    icon: icon('bootstrap'),
    category: 'media',
    align: {
        type: 'string',
        default: 'center'
    },
    attributes: {
        exampleText: {
            type: 'string',
            default: ''
        },
        myRichHeadingText: {
            type: 'string',
            default: ''
        },
        myRichBodyText: {
            type: 'string',
            default: ''
        },
        textAlignment: {
            type: 'string',
        },

        autoComplete: {
            type: "string"
        },
        post: {
            type: "object",
        },
        comboValue: {
            type: 'string',
            default: ''
        }

    },
    keywords: [
        __(' BS-Button BY Jens Wiecker', 'bootscore'),
        __('BS-Button', 'bootscore'),
    ],

    supports: {
        align: ['left', 'right', 'center', 'wide', 'full']
    },
    edit: class extends Component {
        constructor(props) {
            super(...arguments);
            this.props = props;
            this.state = {
                isVisible: false
            }

            this.onChangeExampleText = this.onChangeExampleText.bind(this);
            this.onChangeMyRichHeadingText = this.onChangeMyRichHeadingText.bind(this);
            this.onChangeMyRichBodyText = this.onChangeMyRichBodyText.bind(this);
            this.onChangeTextAlignment = this.onChangeTextAlignment.bind(this);
            this.onChangAutoComplete = this.onChangAutoComplete.bind(this);
            this.checkVisible = this.checkVisible.bind(this);
            this.setPost = this.setPost.bind(this);
            this.OnChangeComboValue = this.OnChangeComboValue.bind(this);

        }

        onChangeExampleText(exampleText) {
            this.props.setAttributes({exampleText});
        }

        onChangeMyRichHeadingText(myRichHeadingText) {
            this.props.setAttributes({myRichHeadingText});
        }

        onChangeMyRichBodyText(myRichBodyText) {
            this.props.setAttributes({myRichBodyText});
        }

        onChangeTextAlignment(textAlignment) {
            this.props.setAttributes({textAlignment});
        }

        onChangAutoComplete(autoComplete) {
            this.props.setAttributes({autoComplete});
        }

        checkVisible(isVisible) {
            this.setState({isVisible});
        }

        setPost(post) {
            this.props.setAttributes({post});
        }

        OnChangeComboValue(comboValue) {
            this.props.setAttributes({comboValue});
        }


        render() {
            const {inputExampleText, attributes: {exampleText = ''} = {}} = this.props;
            const {inputMyRichHeadingText, attributes: {myRichHeadingText = ''} = {}} = this.props;
            const {inputMyRichBodyText, attributes: {myRichBodyText = ''} = {}} = this.props;
            const {alignmentClass, attributes: {textAlignment = ''} = {}} = this.props;
            const alignment = (this.props.attributes.textAlignment != null) ? 'has-text-align-' + this.props.attributes.textAlignment : '';

            const {postClass, attributes: {post = ''} = {}} = this.props;
            const {inputComboClass, attributes: {comboValue = ''} = {}} = this.props;

            const options = [
                {
                    value: 'small',
                    label: 'Small',
                },
                {
                    value: 'normal',
                    label: 'Normal',
                },
                {
                    value: 'large',
                    label: 'Large',
                },
                {
                    value: 'huge',
                    label: 'Huge',
                },
            ];

            const MyComboboxControl = () => {
           //     const [ fontSize, setFontSize ] = useState();
                const [ filteredOptions, setFilteredOptions ] = useState( options );
                return (
                    <ComboboxControl
                        className={inputComboClass}
                        label="Font Size"
                        value={ comboValue }
                        onChange={ setFontSize => this.OnChangeComboValue(setFontSize)}
                        options={ filteredOptions }
                        onFilterValueChange={ ( inputValue ) =>
                            setFilteredOptions(
                                options.filter( ( option ) =>
                                    option.label
                                        .toLowerCase()
                                        .startsWith( inputValue.toLowerCase() )
                                )
                            )
                        }
                    />
                );
            }

            const {inputAutoCompleteClass, attributes: {autoComplete = ''} = {}} = this.props;

            const MyButton = ({text}) => (
                <Button className="btn btn-primary mb-3">{text}</Button>
            );


            const autoConfigs = [
                {
                    name: "Autocomplete",
                    // The prefix that triggers this completer
                    triggerPrefix: "/",
                    // The option data
                    options: [
                        {value: "bi-alarm", label: "", id: 1},
                        {value: '', label: 'Orange', id: 2},
                        {value: '', label: 'Grapes', id: 3},
                    ],
                    // Returns a label for an option like " Orange"
                    getOptionLabel: option => (
                        <span>
						<span className="bi-alarm"> {option.value} </span> {option.label}
					</span>
                    ),
                    // Declares that options should be matched by their name or value
                    getOptionKeywords: option => [option.label, option.value],
                    // Declares that the Grapes option is disabled
                    getOptionCompletion: option => (
                        <i className={option.value} title={option.label}>&nbsp;</i>
                    ),
                }
            ];

            const MyExternalLink = () => (
                <ExternalLink href="https://wordpress.org">WordPress.org</ExternalLink>
            );
            //const {attributes, setAttributes} = props;
            return (

                <Fragment>

                    <div className="hupa-bs-button">
                        <div className={alignment}>
                            <BlockControls>
                                <AlignmentToolbar
                                    className={alignmentClass}
                                    value={textAlignment}
                                    onChange={this.onChangeTextAlignment}
                                />

                                <ToolbarGroup>
                                    <ToolbarButton icon={icon('diagramm')} label="Bold"/>
                                </ToolbarGroup>

                                <ToolbarGroup>
                                    <ToolbarDropdownMenu
                                        icon={more}
                                        label="Select a direction"
                                        controls={[
                                            {
                                                title: 'Up',
                                                icon: arrowUp,
                                                onClick: () => console.log('up'),
                                            },
                                            {
                                                title: 'Right',
                                                icon: arrowRight,
                                                onClick: () => console.log('right'),
                                            },
                                            {
                                                title: 'Down',
                                                icon: arrowDown,
                                                onClick: () => console.log('down'),
                                            },
                                            {
                                                title: 'Left',
                                                icon: arrowLeft,
                                                onClick: () => console.log('left'),
                                            },
                                        ]}
                                    />
                                </ToolbarGroup>

                            </BlockControls>

                            {/*
                            <div className={inputExampleText}>
                                <TextControl
                                    value={exampleText}
                                    onChange={this.onChangeExampleText}
                                />
                            </div>

                            <MyButton text={(exampleText)}/>
                            */}

                            <div className={inputMyRichHeadingText}>
                                <RichText
                                    tagName="h4"
                                    placeholder="Write your heading here"
                                    value={myRichHeadingText}
                                    onChange={this.onChangeMyRichHeadingText}
                                />
                            </div>

                            <RichText
                                className={inputMyRichBodyText}
                                tagName="p"
                                placeholder="Write your paragraph here"
                                value={myRichBodyText}
                                onChange={this.onChangeMyRichBodyText}
                            />

                        </div>
                        <div className={inputAutoCompleteClass}>
                            <RichText
                                autocompleters={autoConfigs}
                                value={autoComplete}
                                onChange={this.onChangAutoComplete}
                                placeholder={__(`Type ${autoConfigs[0].triggerPrefix} to choose a ${autoConfigs[0].name}`)}
                            />
                        </div>
                        <Button isSecondary onClick={(e) => this.checkVisible(!this.state.isVisible)}>
                            Toggle Popover!
                            {this.state.isVisible && (
                                <Popover>
                                    <div style={{padding: "16px"}}>
                                        <i className="bi-alarm">&nbsp;</i> Popover is toggled!
                                    </div>
                                </Popover>
                            )}
                        </Button>

                        <SandBox
                            html="<p>Content</p>"
                            title="Sandbox"
                            type="embed"
                           // scripts={[console.log("It's a sandbox")]}
                        />

                        <MyExternalLink/>

                        <div className={postClass}>
                            <LinkControl
                                searchInputPlaceholder="Search here..."
                                value={post}
                                settings={[
                                    {
                                        id: 'opensInNewTab',
                                        title: 'New tab?',
                                    },
                                    {
                                        id: 'customDifferentSetting',
                                        title: 'Has this custom setting?'
                                    }
                                ]}
                                onChange={(newPost) => this.setPost(newPost)}
                                withCreateSuggestion={true}
                                createSuggestion={(inputValue) => this.setPost({
                                    post: {
                                        ...post,
                                        title: inputValue,
                                        type: "custom-url",
                                        id: Date.now(),
                                        url: inputValue
                                    }
                                })}
                                createSuggestionButtonText={(newValue) => `${__("New:")} ${newValue}`}
                            >
                            </LinkControl>
                            <Dashicon
                                icon="admin-home"
                                style={{fontSize: "54px", width: "54px", height: "54px"}}
                            />

                            <MyComboboxControl/>
                        </div>
                    </div>
                </Fragment>
            );
        }
    },
    save: (props) => {
        const {attributes} = props;
        return (
            <div>
                < div> {attributes.exampleText}  </div>

                <RichText.Content
                    tagName="h2"
                    value={attributes.myRichHeadingText}
                />
                <RichText.Content
                    tagName="p"
                    value={attributes.myRichBodyText}
                />
            </div>
        )
    }
});

